
.jumbotron {
	background-image: url("https://screenrant.com/wp-content/uploads/2017/05/Super-Smash-Bros.jpg");
    background-blend-mode: lighten;
    background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	margin-bottom: 0px;
    text-align: center;
}

.jumbotron h1 {
	font-family: 'Times New Roman', sans-serif;
	font-size: 50px;
	text-transform: uppercase;
	font-weight: 900;
	color: green;
}

.jumbotron p {
	font-family: 'Times New Roman', sans-serif;
	font-size: 22px;
	font-weight: 500;
	text-transform: uppercase;	
	color: blue
}


